<template>
  <div class="fk">
    <div class="fk">
      <headers :title="title"></headers>
      <div class="sp">
        <h2><van-checkbox v-model="checked1">伊衫奇恋男衣店</van-checkbox><img src="../assets/mdx/travel_search_icon_arrow_right.png" alt=""></h2>
        <van-checkbox v-model="checked1">
          <img class="yifu" src="../assets/mdx/yifu.png" alt="">
          <div class="detail">
            <p class="name">
              <span>男士春秋纯棉套头长袖卫衣男圆领宽松休闲运动大码卫衣潮学生上衣</span><van-icon name="edit" />
            </p>
            <p class="haoma">黑绒ty;2XL</p>
            <p class="jiage"><span>￥<i>89</i>.9<a href="#">￥123</a></span><span>1</span></p>
          </div>
        </van-checkbox>
      </div>
      <div class="sp top0">
        <h2><van-checkbox v-model="checked2">伊衫奇恋男衣店</van-checkbox><img src="../assets/mdx/travel_search_icon_arrow_right.png" alt=""></h2>
        <van-checkbox v-model="checked2">
          <img class="yifu" src="../assets/mdx/yifu.png" alt="">
          <div class="detail">
            <p class="name">
              <span>男士春秋纯棉套头长袖卫衣男圆领宽松休闲运动大码卫衣潮学生上衣</span><van-icon name="edit" />
            </p>
            <p class="haoma">黑绒ty;2XL</p>
            <p class="jiage"><span>￥<i>89</i>.9<a href="#">￥123</a></span><span>1</span></p>
          </div>
        </van-checkbox>
      </div>
      <div class="like">
        <img src="../assets/mdx/like.png" alt="">
      </div>
      <div class="duibi">
        <h3><img src="../assets/mdx/yifu.png" alt="">关注该宝贝的人还在对比</h3>
        <img src="../assets/mdx/yifu2.png" alt="">
        <img src="../assets/mdx/yifu2.png" alt="">
      </div>
    </div>
    <div class="footer">
      <van-checkbox v-model="checked">全选<i>合计：<span>￥0</span></i></van-checkbox>
      <a href="#">结算（0）</a>
    </div>
  </div>
</template>

<style lang='less' scoped>
  .fk {
    background: #f6f6f6;
    flex: 1;
    font-family: 'PingFang-SC-Medium';
    display: flex;
    flex-direction: column;

    .sp {
      background: #fff;
      margin-top: 90px;
      padding-left: 30px;
      font-size: 24px;
      color: #333;
      margin-bottom: 20px;

      .van-checkbox {
        display: flex;
        align-items: center;
      }

      h2 {
        height: 82px;
        line-height: 82px;
        display: flex;
        align-items: center;
        border-bottom: 1px solid #eeeeee;
        .van-checkbox {
          width: 312px;
        }
        img {
          width: 18px;
          height: 30px;
        }
      }

      .yifu {
        width: 224px;
        height: 206px;
        float: left;
      }
      .detail {
        flex: 1;
        padding-right: 30px;
        .name {
          display: flex;
          justify-content: space-between;
          font-size: 24px;
          line-height: 34px;
          margin-top: 16px;
          .van-icon-edit {
            width: 26px;
          }
        }
        .haoma {
          font-size: 28px;
          color: #999999;
          margin-top: 15px;
          margin-bottom: 30px;
        }
        .jiage {
          font-size: 24px;
          line-height: 24px;
          color: #ec612a;
          display: flex;
          justify-content: space-between;
          i {
            font-size: 28px;
            padding-left: 8px;
          }
          a {
            font-size: 24px;
            color: #a6a6a5;
            padding-left: 12px;
            text-decoration: line-through;
          }
          span:last-child {
            font-size: 24px;
            color: #a6a6a5;
            padding-left: 13px;
            background: url('../assets/mdx/cheng.png') no-repeat center left;
            background-size: 13px 16px;
          }
        }
      }
    }
    .top0 {
      margin-top: 0;
    }
    .like {
      height: 92px;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 350px;
      }
    }
    .duibi {
      background: #fff;
      h3 {
        height: 96px;
        font-size: 26px;
        color: #999;
        display: flex;
        align-items: center;
        img {
          width: 66px;
          margin: 0 22px;
        }
      }
      img {
        width: 369px;
        float: left;
      }
      img:last-child {
        float: right;
      }
    }
  }

  .footer {
    height: 108px;
    background: #fff;
    display: flex;
    font-size: 30px;
    line-height: 108px;
    align-items: center;
    padding-left: 30px;
    .van-checkbox {
      flex: 1;
      color: #757575;
      i {
        margin-left: 196px;
        color: #000;
        span {
          color: #653a72;
        }
      }
    }
    a {
      width: 200px;
      background: #653a72;
      color: #fff;
      text-align: center;
    }
  }
</style>

<script>
  import "lib-flexible"
  import headers from '../components/editHeader.vue'
  export default {
    components: {
      headers
    },
    data() {
      return {
        title: "购物袋",
        checked1: '',
        checked2: '',
        checked: ''
      }
    },
    watch: {
      checked: function () {
        if(this.checked === true){
          this.checked1 = true;
          this.checked2 = true;
        }else {
          this.checked1 = false;
          this.checked2 = false;
        }
      }
    }
  }
</script>



